'use client';

import { useState } from 'react';
import TaskItem from './task-item';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useLanguage } from '@/contexts/language-context';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../ui/tabs';

export type Task = {
  id: number;
  title: string;
  description: string;
  points: number;
  category: string;
  crop: string;
  image: string;
  imageHint: string;
};

const initialTasks: Task[] = [
  { id: 1, title: 'Check Soil Moisture', description: 'Ensure soil is moist but not waterlogged.', points: 10, category: 'Soil Health', crop: 'Paddy', image: 'https://images.unsplash.com/photo-1556950961-8c092986809f?q=80&w=2070&auto=format&fit=crop', imageHint: 'moist soil' },
  { id: 2, title: 'Apply Organic Fertilizer', description: 'Use compost or manure to enrich the soil.', points: 25, category: 'Fertilization', crop: 'Paddy', image: 'https://images.unsplash.com/photo-1625246333195-78d9c38ad449?q=80&w=2070&auto=format&fit=crop', imageHint: 'organic fertilizer' },
  { id: 3, title: 'Scout for Pests', description: 'Look for common pests like stem borers.', points: 15, category: 'Pest Control', crop: 'Paddy', image: 'https://images.unsplash.com/photo-1521655394015-93cce38f76bf?q=80&w=2070&auto=format&fit=crop', imageHint: 'paddy pest' },
  { id: 4, title: 'Repair Irrigation Drip Lines', description: 'Check for leaks and clogs to conserve water.', points: 20, category: 'Water Management', crop: 'Vegetables', image: 'https://images.unsplash.com/photo-1621258939334-31034255393d?q=80&w=2070&auto=format&fit=crop', imageHint: 'drip irrigation' },
  { id: 5, title: 'Add Mulch Layer', description: 'Apply a layer of straw or leaves to retain moisture.', points: 15, category: 'Soil Health', crop: 'Vegetables', image: 'https://images.unsplash.com/photo-1492496913980-501348b61469?q=80&w=1974&auto=format&fit=crop', imageHint: 'mulching vegetable garden' },
  { id: 6, title: 'Prune Lower Leaves', description: 'Improve air circulation to prevent fungal diseases.', points: 10, category: 'Plant Health', crop: 'Vegetables', image: 'https://images.unsplash.com/photo-1588650690962-972b9a7647a7?q=80&w=1974&auto=format&fit=crop', imageHint: 'pruning tomato plant' },
  { id: 7, title: 'Prepare Land for Planting', description: 'Plow and level the field for the upcoming season.', points: 30, category: 'Preparation', crop: 'Wheat', image: 'https://images.unsplash.com/photo-1470082719200-b52b8de55524?q=80&w=2072&auto=format&fit=crop', imageHint: 'plowing field' },
  { id: 8, title: 'Test Soil pH', description: 'Check if the soil pH is optimal for wheat growth (6.0-7.0).', points: 20, category: 'Soil Health', crop: 'Wheat', image: 'https://images.unsplash.com/photo-1617838799443-bf4c798c9fee?q=80&w=2070&auto=format&fit=crop', imageHint: 'soil test kit' },
  { id: 9, title: 'Check for Sugarcane Borers', description: 'Inspect stalks for signs of borer infestation.', points: 20, category: 'Pest Control', crop: 'Sugarcane', image: 'https://images.unsplash.com/photo-1571089950313-a4488de8c5c7?q=80&w=1974&auto=format&fit=crop', imageHint: 'sugarcane pest' },
  { id: 10, title: 'Detrashing Sugarcane', description: 'Remove dry leaves to improve air circulation.', points: 15, category: 'Plant Health', crop: 'Sugarcane', image: 'https://images.unsplash.com/photo-1589993336448-316889a7213e?q=80&w=2070&auto=format&fit=crop', imageHint: 'sugarcane field' },
  { id: 11, title: 'Monitor for Bollworms', description: 'Check cotton plants for bollworm eggs and larvae.', points: 20, category: 'Pest Control', crop: 'Cotton', image: 'https://images.unsplash.com/photo-1617822295325-2436def35848?q=80&w=1974&auto=format&fit=crop', imageHint: 'cotton pest' },
  { id: 12, title: 'Apply Neem Oil Spray', description: 'Use neem oil as a natural insecticide for cotton.', points: 15, category: 'Pest Control', crop: 'Cotton', image: 'https://images.unsplash.com/photo-1600955932587-38967avantage?q=80&w=2070&auto=format&fit=crop', imageHint: 'natural insecticide' },
];

const crops = ['Paddy', 'Vegetables', 'Wheat', 'Sugarcane', 'Cotton'];

export default function TaskList() {
  const [selectedCrop, setSelectedCrop] = useState('Paddy');
  const { language, t } = useLanguage();
  const [pendingTasks, setPendingTasks] = useState<Task[]>(initialTasks);
  const [completedTasks, setCompletedTasks] = useState<Task[]>([]);

  const handleTaskComplete = (taskId: number) => {
    const taskToMove = pendingTasks.find(t => t.id === taskId);
    if (taskToMove) {
      setPendingTasks(pendingTasks.filter(t => t.id !== taskId));
      setCompletedTasks([taskToMove, ...completedTasks]);
    }
  };

  const filteredPendingTasks = pendingTasks.filter(task => task.crop === selectedCrop);
  const filteredCompletedTasks = completedTasks.filter(task => task.crop === selectedCrop);

  return (
    <Card>
      <CardHeader>
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
          <CardTitle>{t('tasksTitle', 'dashboard')}</CardTitle>
          <div className="flex items-center gap-2">
            <span className="text-sm font-medium">{t('crop', 'dashboard')}:</span>
            <Select value={selectedCrop} onValueChange={setSelectedCrop}>
              <SelectTrigger className="w-[180px]">
                <SelectValue placeholder={t('selectCrop', 'dashboard')} />
              </SelectTrigger>
              <SelectContent>
                {crops.map(crop => (
                  <SelectItem key={crop} value={crop}>
                    {t(`crop${crop}`, 'register')}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <Tabs defaultValue="pending">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="pending">{t('pendingTasks', 'dashboard')}</TabsTrigger>
            <TabsTrigger value="completed">{t('completedTasks', 'dashboard')}</TabsTrigger>
          </TabsList>
          <TabsContent value="pending" className="mt-4">
            {filteredPendingTasks.length > 0 ? (
              <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                {filteredPendingTasks.map(task => (
                  <TaskItem key={task.id} task={task} onTaskComplete={handleTaskComplete} isCompleted={false} />
                ))}
              </div>
            ) : (
              <p className="text-center text-muted-foreground py-8">{t('noTasks', 'dashboard')}</p>
            )}
          </TabsContent>
          <TabsContent value="completed" className="mt-4">
            {filteredCompletedTasks.length > 0 ? (
              <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                {filteredCompletedTasks.map(task => (
                  <TaskItem key={task.id} task={task} onTaskComplete={() => {}} isCompleted={true} />
                ))}
              </div>
            ) : (
              <p className="text-center text-muted-foreground py-8">{t('noCompletedTasks', 'dashboard')}</p>
            )}
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
}
