'use server';

import {
  verifyLandDocAuthenticity,
  VerifyLandDocAuthenticityInput,
} from '@/ai/flows/verify-land-doc-authenticity';
import {
  validateFarmCropContext,
  ValidateFarmCropContextInput,
} from '@/ai/flows/validate-farm-crop-context';
import { generateSustainableTips, SustainableTipsInput } from '@/ai/flows/generate-sustainable-tips';
import { verifyAadhaarAuthenticity, VerifyAadhaarAuthenticityInput } from '@/ai/flows/verify-aadhaar-authenticity';

export async function verifyDocumentAction(
  input: VerifyLandDocAuthenticityInput
) {
  return await verifyLandDocAuthenticity(input);
}

export async function verifyAadhaarAction(
    input: VerifyAadhaarAuthenticityInput
) {
    return await verifyAadhaarAuthenticity(input);
}

export async function validateTaskAction(input: ValidateFarmCropContextInput) {
  return await validateFarmCropContext(input);
}

export async function generateTipsAction(input: SustainableTipsInput) {
    return await generateSustainableTips(input);
}
