'use client';

import { useState } from 'react';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Volume2, Upload, CheckCircle, Loader2, XCircle, VolumeX } from 'lucide-react';
import { useSpeech } from '@/hooks/use-speech';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import Image from 'next/image';
import { validateTaskAction } from '@/app/actions';
import type { ValidateFarmCropContextOutput } from '@/ai/flows/validate-farm-crop-context';
import { useLanguage } from '@/contexts/language-context';
import type { Task } from './task-list';


interface TaskItemProps {
  task: Task;
  onTaskComplete: (taskId: number) => void;
  isCompleted: boolean;
}

export default function TaskItem({ task, onTaskComplete, isCompleted }: TaskItemProps) {
  const { speak, stop, isSpeaking } = useSpeech();
  const { language, t } = useLanguage();
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [file, setFile] = useState<File | null>(null);
  const [preview, setPreview] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<ValidateFarmCropContextOutput | null>(null);
  const { toast } = useToast();

  const translatedTitle = t(task.title, 'tasks');
  const translatedDesc = t(task.description, 'tasks');
  const translatedCategory = t(task.category, 'tasks');

  const handleSpeak = () => {
    if (isSpeaking) {
      stop();
    } else {
      speak({ text: `${translatedTitle}. ${translatedDesc}. This task is worth ${task.points} points.`, lang: language });
    }
  };
  
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile) {
      setFile(selectedFile);
      setResult(null);
      const reader = new FileReader();
      reader.onloadend = () => {
        setPreview(reader.result as string);
      };
      reader.readAsDataURL(selectedFile);
    }
  };

  const handleVerify = async () => {
    if (!file) {
      toast({ title: t('No file selected', 'tasks'), variant: "destructive" });
      return;
    }
    setIsLoading(true);
    setResult(null);
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = async () => {
      const photoDataUri = reader.result as string;
      try {
        const validationResult = await validateTaskAction({
          photoDataUri,
          cropType: task.crop,
          taskDescription: task.title,
        });
        setResult(validationResult);
        if (validationResult.isValidContext) {
            onTaskComplete(task.id);
            toast({
              title: t('Task Verified & Completed!', 'tasks'),
              description: validationResult.reason,
            });
            setIsDialogOpen(false);
        } else {
             toast({
              title: t('Verification Failed', 'tasks'),
              description: validationResult.reason,
              variant: "destructive",
            });
        }
      } catch (error) {
        toast({ title: t('Verification error', 'tasks'), description: t('Something went wrong.', 'tasks'), variant: "destructive" });
      } finally {
        setIsLoading(false);
      }
    };
  };

  return (
    <Card className="flex flex-col relative overflow-hidden">
       {isCompleted && (
        <div className="absolute top-2 right-2 bg-green-500 text-white p-1 rounded-full z-20">
          <CheckCircle className="h-5 w-5" />
        </div>
      )}
       <div className="relative w-full h-40 group">
        <Image
          src={task.image}
          alt={task.title}
          fill
          style={{ objectFit: 'cover' }}
          className="rounded-t-lg"
          data-ai-hint={task.imageHint}
        />
        {isCompleted && <div className="absolute inset-0 bg-black/50 z-10" />}
      </div>
      <CardHeader>
        <div className="flex justify-between items-start">
          <div>
            <CardTitle className="text-lg">{translatedTitle}</CardTitle>
            <CardDescription>{translatedDesc}</CardDescription>
          </div>
          <Badge variant="secondary" className="whitespace-nowrap">{translatedCategory}</Badge>
        </div>
      </CardHeader>
      <CardContent className="flex-grow">
        <p className="font-semibold text-primary">{task.points} {t('Points', 'tasks')}</p>
      </CardContent>
      <CardFooter className="flex justify-end gap-2">
        <Button variant="outline" size="icon" onClick={handleSpeak}>
          {isSpeaking ? <VolumeX className="h-4 w-4" /> : <Volume2 className="h-4 w-4" />}
          <span className="sr-only">{isSpeaking ? t('stopReading', 'common') : t('Speak task', 'tasks')}</span>
        </Button>
        {!isCompleted && (
            <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
            <DialogTrigger asChild>
                <Button>
                <Upload className="mr-2 h-4 w-4" />
                {t('Upload Proof', 'tasks')}
                </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
                <DialogHeader>
                <DialogTitle>{t('Upload Proof for', 'tasks').replace('{taskTitle}', translatedTitle)}</DialogTitle>
                <DialogDescription>
                    {t('Upload a photo from your field as proof of task completion. You can use your camera or upload a file.', 'tasks')}
                </DialogDescription>
                </DialogHeader>
                <div className="grid gap-4 py-4">
                <div className="grid w-full max-w-sm items-center gap-1.5">
                    <Label htmlFor="picture">{t('Picture', 'tasks')}</Label>
                    <Input id="picture" type="file" accept="image/*" capture="environment" onChange={handleFileChange} />
                </div>
                {preview && (
                    <div className="relative w-full h-48 border-2 border-dashed rounded-lg flex items-center justify-center">
                        <Image src={preview} alt="Proof preview" fill style={{ objectFit: 'contain' }} className="rounded-lg p-1" />
                    </div>
                )}
                {result && (
                    <div className={`p-3 rounded-md flex items-center gap-2 text-sm ${result.isValidContext ? 'bg-green-100 text-green-800 dark:bg-green-900/50 dark:text-green-300' : 'bg-red-100 text-red-800 dark:bg-red-900/50 dark:text-red-300'}`}>
                    {result.isValidContext ? <CheckCircle className="h-4 w-4" /> : <XCircle className="h-4 w-4" />}
                    <span>{result.reason}</span>
                    </div>
                )}
                </div>
                <Button onClick={handleVerify} disabled={isLoading || !file}>
                    {isLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <CheckCircle className="mr-2 h-4 w-4" />}
                    {t('Verify with AI', 'tasks')}
                </Button>
            </DialogContent>
            </Dialog>
        )}
      </CardFooter>
    </Card>
  );
}
